/* ===== Blocks ===== */
.background {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
  user-select: none;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0;
  visibility: hidden;
  transition: opacity 1s ease-out; }
  .background.lazyloaded {
    opacity: 1;
    visibility: visible; }
  .background.overlay.lazyloaded {
    opacity: 0.1; }
  .background_left {
    background-position-x: left; }
  .background_right {
    background-position-x: right; }
  .background_top {
    background-position-y: top; }
  .background_bottom {
    background-position-y: bottom; }

.cover, .contain {
  display: block;
  position: relative; }
  .cover img, .contain img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
